.assortement {
	background-color: $bg-dark;
	padding: 87px 0 124px;
	position: relative;
	z-index: 0;

	.title-text-border {
		color: $dark;
		text-shadow: -1px -1px 0 $shadow-dark-text, 1px -1px 0 $shadow-dark-text, -1px 1px 0 $shadow-dark-text,
			1px 1px 0 $shadow-dark-text;
	}

	&__card {
		position: relative;
		display: flex;
		height: 260px;
		width: 100%;
		color: $text-color-white;
		margin-bottom: 30px;
		&::before {
			content: "";
			@include transition(0.4s);
			opacity: 0;
		}

		> img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:hover {
			color: $text-color-white;
			box-shadow: 0px 20px 50px $black-opacity;

			&::before {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: $black-opacity;
				opacity: 1;
			}

			.assortement__hover {
				opacity: 1;
			}
		}
	}

	&__desc {
		position: absolute;
		top: 38px;
		left: 39px;
		right: 39px;

		> h4 {
			font-size: $font-size-h3;
			line-height: $line-height-h3;
			font-weight: 900;
		}

		> p {
			margin-top: 16px;
			width: 60%;
		}
	}

	&__hover {
		opacity: 0;
		position: absolute;
		left: 39px;
		bottom: 36px;
		font-weight: 100;
		font-size: 14px;
		line-height: 17px;
		@include transition(opacity 0.4s);

		img {
			margin-left: 10px;
		}
	}
}

.assortment-page {
	padding-top: 20px;

	.download-catalog {
		padding: 120px 0 218px;
	}
}

.assortment-heading {
	padding-top: 60px;

	&__title {
		font-size: $font-size-h2;
		line-height: $line-height-h2;
		margin-bottom: 40px;
		font-weight: 900;
	}

	.assortement__card {
		height: 357px;
		background-color: $bg-dark;

		> img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		> h4 {
			position: absolute;
			bottom: 22px;
			left: 35px;
			right: 35px;
			font-weight: 900;
			font-size: $font-size-h3;
			line-height: $line-height-h3;
			min-height: 60px;
			@include transition(0.4s);
		}

		&:hover {
			> h4 {
				bottom: 60px;
			}
		}
	}

	&__desc {
		padding: 59px 100px 94px;
		h3 {
			font-size: $font-size-h3;
			line-height: $line-height-h3;
			font-weight: 500;
			margin-bottom: 20px;
		}
		> img {
			width: 100%;
			margin: 35px 0;
		}
		> p {
			margin-bottom: 20px;
		}
	}

	&__page {
		.download-catalog {
			padding: 150px 0 250px;
		}

		.ready-order {
			padding: 0 0 136px;
		}

		.calendar-section {
			padding: 71px 0 125px;
		}
	}
}

@media only screen and (max-width: 1199.8px) {
	.assortement {
		padding: 37px 0 59px;

		&__card {
			height: 200px;
		}

		&__desc {
			position: absolute;
			top: 24px;
			left: 30px;
			right: 30px;

			> h4 {
				font-size: $base-font-size;
				line-height: $base-line-height;
			}

			> p {
				margin-top: 10px;
				width: 80%;
			}
		}

		&__hover {
			left: 30px;
			bottom: 28px;
		}
	}

	.assortment-heading {
		padding-top: 46px;
		&__title {
			font-size: $font-size-h2 - 6px;
			line-height: $line-height-h2 - 6px;
			margin-bottom: 32px;
		}
		.assortement__card {
			height: 270px;
			> h4 {
				bottom: 16px;
				left: 21px;
				right: 21px;
				font-size: $font-size-h3 - 4px;
				line-height: $line-height-h3 - 5px;
				min-height: 50px;
			}
		}
		&__desc {
			padding: 59px 30px 20px;
			margin-top: 30px;
		}

		&__page {
			.download-catalog {
				padding: 72px 0 98px;
			}

			.ready-order {
				padding: 0 0 76px;
			}

			.calendar-section {
				padding: 80px 0 102px;
			}
			.FAQ {
				padding: 76px 0;
			}
		}
	}
}

@media only screen and (max-width: 991.8px) {
	.assortement {
		.title-text-border {
			font-size: 96px;
			line-height: 115px;
			margin-bottom: -26px;
		}

		&__card {
			height: 150px;
		}

		&__desc {
			position: absolute;
			top: 13px;
			left: 13px;
			right: 13px;

			&_big {
				top: 24px;
				left: 30px;
				right: 30px;
			}
		}

		&__hover {
			left: 13px;
			bottom: 15px;
		}
	}

	.assortment-page {
		.download-catalog {
			padding: 30px 0 102px;
		}
	}

	.assortment-heading {
		&__title {
			font-size: $font-size-h3;
			line-height: $line-height-h3;
		}
		.assortement__card {
			height: 210px;
			> h4 {
				right: 14px;
				font-size: $base-font-size;
				line-height: $base-line-height;
				min-height: 22px;
			}
		}
		&__desc {
			h3 {
				font-size: $base-font-size;
				line-height: $base-line-height;
			}
		}
	}
}

@media only screen and (max-width: 767.8px) {
	.assortement {
		padding: 28px 0 0;

		.title-text-border {
			font-size: 56px;
			line-height: 65px;
			margin-bottom: 20px;
		}

		&__card {
			height: 150px;

			&_big {
				height: 180px;
			}
		}

		&__desc {
			position: absolute;
			top: 16px;
			left: 20px;
			right: 20px;

			> p {
				width: 100%;
			}

			> h4 {
				max-width: 110px;
			}
		}

		&__hover {
			left: 16px;
			bottom: 20px;
		}
	}

	.assortment-heading {
		padding: 18px 0 0;
		&__title {
			font-size: $font-size-h3 - 4px;
			line-height: $line-height-h3 - 5px;
		}
		.assortement__card {
			height: 150px;
			> h4 {
				right: 14px;
				bottom: auto;
				top: 14px;
				max-width: 45%;
			}
		}
		&__desc {
			padding: 29px 20px;
			margin-top: 0;
		}
		&__page {
			.download-catalog {
				padding: 35px 0 152px;
			}

			.ready-order {
				padding: 0 0 30px;
			}

			.calendar-section {
				padding: 54px 0 88px;
				&.border-bot {
					border: none;
				}
			}
			.FAQ {
				padding: 0 0 56px;
			}
		}
	}
}

@media only screen and (max-width: 575.8px) {
	.assortement {
		.title-text-border {
			font-size: 36px;
			line-height: 43px;
		}

		&__card {
			height: 110px;

			&_big {
				height: 155px;
			}
		}

		&__hover {
			display: none;
		}
	}

	.assortment-page {
		.title-text-border {
			font-size: $font-size-h2;
			line-height: $line-height-h2;
			margin-bottom: -9px;
		}

		.download-catalog {
			padding: 20px 0 117px;
		}
	}

	.assortment-heading {
		padding: 18px 0 0;
		&__title {
			font-size: $base-font-size;
			line-height: $base-line-height;
		}
		.assortement__card {
			height: 110px;
		}
		&__desc {
			h3 {
				font-size: $base-font-size - 4px;
				line-height: $base-line-height - 4px;
			}
		}
	}
}
